@import url(../App.css);

.country {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: start;
  width: 90%;
  gap: .5rem;
}
.back-btn {
  color: var(--text);
  background-color: var(--bg);  
  gap: 1rem;
  display: flex;
  cursor: pointer;
  font-size: 1.5rem;
  padding: .5rem 1rem;   
  align-items: center;
  border: 1px solid var(--input);
  border-radius: .5rem;
}
@media (hover: hover) {
  .back-btn:hover {
    background-color: var(--text);
    color: var(--bg);
  }
}
@media (hover: none) {
  .back-btn:active {
    background-color: var(--text);
    color: var(--bg);
  }
}
.link {
  text-decoration: none;  
}
.flex-col {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: space-between;
}
.country-grid {
  width: 100%;
  max-width: 375px;
  margin: 2rem auto;
  padding: 1rem;
  border: 1px solid var(--input);
  border-radius: .5rem;
}
.flag {
  height: 14rem;
}
.main-text-area {
  gap: 1rem;
  padding: 1rem 0;
}
.main-text-area h1 {
  font-size: 2rem;
}
.sub-text-area {
  font-size: 1.4rem;
  margin-bottom: 1rem;
}
.sub-text-area p {
  margin-top: 1rem;
}
span {
  color: var(--input);
}
.borders-div,
.lang-div {
  display: flex;
  font-size: 1.2rem;
  justify-content: start;
  align-items: end;
  gap: 1rem;
}
.borders-div ul,
.lang-div ul {
  display: flex;
  justify-content: start;
  align-items: start;
  gap: 1rem;
  flex-wrap: wrap;
}
@media (min-width: 1240px) {
  .back-btn {
    font-size: 2rem;
    padding: 1rem 2rem;  
  }
  .country-grid {
    max-width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
    grid-template-areas: "flag  text";
  }
  .flag {
    width: 100%;
    height: 100%;
    grid-area: flag;
  }
  .main-text-area {
    grid-area: text;
    width: 100%;
    gap: .5rem;
  }
  .main-text-area h1 {
    font-size: 4rem;
  }
  .main-text-area div p {
    font-weight: 700;
    font-size: 1.6rem;
  }
  .main-text-area div p span {
    font-weight: 400;
  }  
  .sub-text-area {
    gap: 4rem;
    width: 100%;
    display: flex;
    justify-content: start;
    line-height: 2rem;
    margin-bottom: 2rem;
  }
  .borders-div, .lang-div { gap: .5rem; align-items: start; }
  .borders-div ul { gap: .5rem; }
}